@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.eventBtn,
.resetBtn {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  background-color: #E4C1AE;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  margin-top: 32px;
  transition: transform 0.15s ease-in-out;
}
.eventBtn:hover,
.resetBtn:hover {
  cursor: pointer;
  transform: scale(1.15);
}

.resetBtn {
  visibility: hidden;
  position: absolute;
  left: 75%;
  top: 25%;
}

.closeBtn {
  cursor: pointer;
  position: absolute;
  right: 70px;
  top: 46px;
  width: 24px;
  height: 24px;
}
.closeBtn:after, .closeBtn:before {
  content: "";
  height: 24px;
  left: 11px;
  border-left: 2px solid #000;
  position: absolute;
}
.closeBtn:after {
  transform: rotate(45deg);
}
.closeBtn:before {
  transform: rotate(-45deg);
}

.main-module {
  background-color: #fff;
  width: 100% vw;
  position: relative;
  top: 10vh;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: wrap;
  gap: 95px;
  justify-content: center;
}
.main-module img {
  width: 505px;
  height: 330px;
}
.main-module .content {
  display: flex;
  flex-direction: column;
  width: 380px;
  height: 250px;
}
.main-module .content .title {
  margin-top: 40px;
  font-size: 42px;
  line-height: 50px;
  font-weight: 700;
}
.main-module .content .copy {
  line-height: 150%;
  font-weight: 300;
  margin-top: 24px;
  display: flex;
  align-items: center;
}

.popupContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  background-color: #fff;
  width: 63.8888888889%;
  height: 210px;
  z-index: 3;
  padding: 56px 95px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.popupContent.active {
  transform: translate(-50%, -50%) scale(1);
}
.popupContent .title {
  font-size: 42px;
  line-height: 50px;
}
.popupContent .copy {
  line-height: 24px;
}

.popup.active {
  display: block;
}

.overlay {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(18, 18, 18, 0.5);
  pointer-events: none;
}
.overlay.active {
  opacity: 1;
  pointer-events: all;
}

body {
  font-family: "Roboto", sans-serif;
  color: #121212;
  font-size: 16px;
}

@media (max-width: 1280px) {
  .main-module {
    top: 8vh;
  }
}
@media (max-width: 1024px) {
  .main-module {
    top: 6vh;
  }
}